}

.slider {
  width: 500px;
  height: 300px;
  position: relative;
  overflow: hidden;
}

.slider-img {
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity .5s;
  position: absolute;
  top: 0;
  left: 0;
}

.slider-img.active {
  opacity: 1;
}

.slider img {
  z-index: 1;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.slider .prev {
  left: 10px;
}

.slider .next {
  right: 10px;
}

